
@import '../assets/reset.css';
	/*main*/
.main{
		padding: 0 1rem;
	    background: #FFFFFF;
	    margin-top: 2rem;
}
.cityDes {
		width: 100%;
	    color: #9d9d9d;
	    font-size: 1.4rem;
}
.cityStartDes {
	    float: left;
}
.cityEndDes {
	    float: right;
	    margin-right: 0px;
}
.cityDes:after {
	    content: ".";
	    display: block;
	    height: 0;
	    clear: both;
	    visibility: hidden;
}
.citySelect {
	    width: 100%;
	    text-align: center;
	    margin-bottom: 0.5rem;
}
.citySelect div {
	    width: 33%;
	    min-width: 6rem;
	    display: table-cell;
	    vertical-align: middle;
	    float: left;
}
.textmain {
	    word-wrap: break-word;
	    word-break: break-all;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
}
.destination{
		float: right!important;
		text-align: center;
}
.city {
	    font-size: 1.4rem;
	    font-weight: bold;
	    color: #989898;
	    display: inline-block;
	    width: 90%;
	    text-overflow: ellipsis;
}
.divImgCenter {
	    text-align: center;
	    width: 20%!important;
}
.convertImg {
    	width: 3rem;
}
.textmainright {
	    -webkit-box-align: end;
	        -ms-flex-align: end;
	            align-items: flex-end;
	    -webkit-justify-content: flex-end;
}
.textmain {
	    word-wrap: break-word;
	    word-break: break-all;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    margin-top: 1rem;
}
.citySelect:after {
	    content: ".";
	    display: block;
	    height: 0;
	    clear: both;
	    visibility: hidden;
}
.line {
	    width: 100%;
	    height: 1px;
	    background-color: #E2E2E2;
}
.dateSelect {
		width: 100%;
	    font-size: 1rem;
	    margin-top: 1rem;
	    height: 1rem;
}
.dateTime {
	    float: left;
}
.dateWeek {
	    float: right;
}
.calendarIcon {
	    width: 0.5rem;
	    margin-right: 0.2rem;
}
#week {
	    margin-right: 0.5rem;
	    color: #1FCC9E;
}
.dateWeek {
	    float: right;
}
.div_btn {
		width: 100%;
	    background-color: #fff;
	    margin-top:2rem;
}
.find {
	    width: 90%;
	    height: 2.5rem;
	    background: #1FCC9E;
	    text-align: center;
	    line-height: 2.5rem;
	    margin: 1rem 0 0 5%;
	    color: #FFFFFF;
	    font-size: 1.4rem;
	    border-radius: 8px;
}



